<template>
  <div>
    <!--  -->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/home' }"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>
    <!--  -->
    <el-card>
      <el-row :gutter="30">
        <!-- <el-col :span="6">
          <el-input placeholder="" v-model="queryInfo.mhDwmc" clearable> -->
<!--            <el-input placeholder="" v-model="queryInfo.query" clearable @clear="getGoodsList">-->
            <!--            <el-button slot="append" icon="el-icon-search" @click="getGoodsList"></el-button>-->
          <!-- </el-input>
        </el-col>
        <el-col :span="6">
          <span></span>
          <el-select v-model="queryInfo.dwmc" clearable placeholder="">
            <el-option v-for="(item,index) in companyOptions" :key="index"  :value="item.dwmc"></el-option>
          </el-select>
        </el-col> -->
        <el-col :span="6">
          <span></span>
          <el-select v-model="queryInfo.zjly" clearable placeholder="">
            <el-option v-for="(item,index) in fundOptions" :key="index" :value="item"></el-option>
          </el-select>
        </el-col>
        <el-col :span="3">
          <el-button type="primary" @click="findIndexText"></el-button>
        </el-col>
        <el-col :span="3">
          <el-button type="danger" @click="clearFilter"></el-button>
        </el-col>
      </el-row>
      <h3></h3>
      <!-- table -->
      <el-table :data="indexTextlist" border stripe :header-cell-style="{background:'#eee',color:'#606266',fontSize: '14px'}">
        <el-table-column type="index"></el-table-column>
        <el-table-column label="" prop="zbwh" ></el-table-column>
        <el-table-column label="" prop="dwmc" width="200px"></el-table-column>
        <el-table-column label="()" prop="hjje" width="200px"></el-table-column>
        <el-table-column label="()" prop="hjybje" width="200px"></el-table-column>
        <el-table-column label="()" prop="syje" width="200px"></el-table-column>
        <el-table-column label="" prop="zjly" width="200px"></el-table-column>
        <el-table-column label="" width="150px">
          <template slot-scope="scope">
            <el-tooltip class="item" effect="dark" content="" placement="top-start">
              <el-button type="primary" icon="el-icon-view" circle size="mini" @click="viewVideoData(scope.row.zbwh)"></el-button>
            </el-tooltip>
          </template>
        </el-table-column>
      </el-table>

      <!--  -->
      <el-pagination @size-change="handleSizeChange" @current-change="handleCurrentChange" :current-page="queryInfo.pageNum" :page-sizes="[2, 5, 10, 15, 20]" :page-size="queryInfo.pageSize" layout="total, sizes, prev, pager, next, jumper" :total="total" background>
      </el-pagination>
    </el-card>

  <!--    -->
    <el-dialog title="" :visible.sync="videoDialogVisible" width="50%">
      <swiper class="swiper" :options="swiperOption">
        <swiper-slide v-for="(item, index) in imgSrcList" :key="index">
          <img :src="item">
        </swiper-slide>
        <div class="swiper-pagination" slot="pagination"></div>
        <div class="swiper-button-prev" slot="button-prev"></div>
        <div class="swiper-button-next" slot="button-next"></div>
      </swiper>
      <span slot="footer" class="dialog-footer">
        <el-button type="primary" @click="videoDialogVisible = false"> </el-button>
       </span>
    </el-dialog>

  </div>
</template>

<script>
import { Swiper, SwiperSlide } from 'vue-awesome-swiper'

export default {
  data() {
    return {
      // 
      fundOptions: [],
      companyOptions: [],
      // 
      queryInfo: {
        // mhDwmc: '',
        // dwmc: '',
        // 
        gjjzh: '',
        zjly: '',
        pageNum: 1,
        pageSize: 10,
        onlyHaveBalance:false
      },
      // 
      indexTextlist: [],
      // 
      total: 0,
      // 
      imgSrcList: [],
      // 
      videoDialogVisible: false,
      // 
      swiperOption: {
        slidesPerView: 1,
        spaceBetween: 30,
        loop: true,
        pagination: {
          el: '.swiper-pagination',
          clickable: true
        },
        navigation: {
          nextEl: '.swiper-button-next',
          prevEl: '.swiper-button-prev'
        }
      }
    }
  },
  components: {
    Swiper,
    SwiperSlide,
  },
  created() {
    this.queryInfo.gjjzh = window.sessionStorage.getItem('gjjzh');
    this.getOptions();
    this.getIndexTextList();
  },
  mounted() {

  },
  methods: {
    async getOptions() {
      const { data: { data: res1} } = await this.$http.get('zbwmx/getAllZjly');
      this.fundOptions = res1;
      // const {data: { data: res2 }} = await this.$http.get('/zbwmx/getAllDwdjxx');
      // this.companyOptions = res2;
    },

    // 
    async getIndexTextList() {
      const {data:meta,data:{data:res} } = await this.$http.post('/zbwmx/findZbwmx', this.queryInfo
      )
      if (meta.code !== 200) {
        return this.$message.error('');
      }
      if (res.total == 0) {
        return this.$message.error('')
      }
      this.$message.success('');
      this.indexTextlist = res.records;
      this.total = res.total;
      console.log(res.records)
    },
    handleSizeChange(newSize) {
      this.queryInfo.pageSize = newSize
      this.getIndexTextList()
    },
    handleCurrentChange(newPage) {
      this.queryInfo.pageNum = newPage
      this.getIndexTextList()
    },
    findIndexText() {
      this.getIndexTextList();
    },
    clearFilter() {
      // this.queryInfo.dwmc = '';
      // this.queryInfo.mhDwmc = '';
      this.queryInfo.zjly = '';
      this.getIndexTextList()
    },
    // 
   async viewVideoData(zbwh) {
      const { data:res } = await this.$http.get('/zbwmx/findZbwFile', {params: {
        channelCode: 'center',
        serviceCode:'btzbwlr',
        zbwh:zbwh
        }})
      if (res.code !== 200) return this.$message.error('')
      this.videoDialogVisible = true
      this.imgSrcList = []
      // 
      for(let i = 0; i < res.data.length; i++) {
        //  
        res.data[i].Files.forEach(item => {
          // 
          const symbolIndex = item.FileName.lastIndexOf('.')
          // .
          const imgType = item.FileName.substr(symbolIndex + 1)
          const imgSrc = `data:image/${imgType};base64,${item.FileBase64}`
          // 
          this.imgSrcList.push(imgSrc)
        })
      }
    }
  }
}
</script>

<style lang="less" scoped>
.swiper-slide {
  display: flex;
  justify-content: center;
}
</style>
