<template>
  <div>
    <!--  -->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/home' }"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>

    <!--  -->
    <el-card class="box-card">
      <div slot="header" class="clearfix">
        <el-row :gutter="30">
          <el-col :span="2">
            <div style='font-size: 20px;font-weight:800;height: 62px;line-height: 40px'>
              
            </div>
          </el-col>
          <el-col :span="6">
            <el-date-picker
              v-model="date"
              type="daterange"
              range-separator=""
              start-placeholder=""
              end-placeholder="">
            </el-date-picker>
          </el-col>
          <el-col :span="4" style="display: flex; justify-content: space-between; align-items: center">
            <div style="width: 60px; font-weight: bold"></div>
            <el-input placeholder="" v-model="queryInfo.name" clearable></el-input>
          </el-col>
          <el-col :span="6" style="display: flex; justify-content: space-between; align-items: center">
            <div style="width: 100px; font-weight: bold"></div>
            <el-input placeholder="" v-model="queryInfo.id_card_number" clearable></el-input>
          </el-col>
          <el-col :span="2"><el-button type="primary" size="small" icon="el-icon-search" @click='search'></el-button></el-col>
          <el-col :span="3"><el-button type="danger" size="small" @click='clearSearch'></el-button></el-col>
        </el-row>
      </div>
      <el-row>
        <el-table :data="historyAllowanceList" border stripe :header-cell-style="{background:'#eee',color:'#606266',fontSize: '14px'}">
            <el-table-column label="" type="index" width="50px"></el-table-column>
            <el-table-column label="" prop="zbwh"></el-table-column>
            <el-table-column label="" prop="zgxm"></el-table-column>
            <el-table-column label="" prop="sfzh" width="200px"></el-table-column>
            <el-table-column label="" prop="btje"></el-table-column>
            <el-table-column label="" width="200px">
              <template slot-scope="scope">
                {{scope.row.rzsj.slice(0,10)}}
              </template>
            </el-table-column>
            <el-table-column label="" prop="yzje"></el-table-column>
            <el-table-column label="" width="100px">
              <template slot-scope="scope">
                <el-button v-if="scope.row.has_inheritor" size="mini" type="warning" icon="el-icon-notebook-1" @click="showHistoryDialog(scope.row)"></el-button>
              </template>
            </el-table-column>
          </el-table>
          <el-pagination @size-change="handleSizeChange" @current-change="handleCurrentChange" :current-page="queryInfo.pageNum" :page-sizes="[2, 5, 10, 15, 20]" :page-size="queryInfo.pageSize" layout="total, sizes, prev, pager, next, jumper" :total="total" background>
          </el-pagination>
      </el-row>
    </el-card>

  <!--    -->
    <el-dialog title="" :visible.sync="historyDialogVisible" width="70%">
      <el-table :data="agentList" border stripe :header-cell-style="{background:'#eee',color:'#606266',fontSize: '14px'}">
        <el-table-column label="" prop="dlrxm" width="100px"></el-table-column>
        <el-table-column label="" prop="dlrsfzh"></el-table-column>
        <el-table-column label="" prop="phoneNumber"></el-table-column>
        <el-table-column label="" prop="dllmkh" width="200px"></el-table-column>
        <el-table-column label="" prop="dllx" width="50px"></el-table-column>
        <el-table-column label="" prop="gxmc" width="50px"></el-table-column>
        <el-table-column label="" prop="dlje"></el-table-column>
        <el-table-column label="" prop="czy" width="100px"></el-table-column>
        <el-table-column label="" prop="bz"></el-table-column>
      </el-table>
      <span slot="footer" class="dialog-footer">
        <el-button @click="historyDialogVisible=false"> </el-button>
        <el-button type="primary" @click="historyDialogVisible=false"> </el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
export default {
  data() {
    return {
      // 
      gjjzh: '',
      // 
      currentAllowanceList: [],
      historyAllowanceList: [],
      // 
      setRightDialogVisible: false,
      // 
      historyDialogVisible: false,
      // 
      rightslist: [],
      // 
      treeProps: {
        label: 'authName',
        children: 'children'
      },
      // Id
      defKeys: [],
      // id
      roleId: '',
      date: '',
      activeName: 'now',
      createable: false,
      // 
      setNameDialogVisible: false,
      setNameForm: {
        name: ''
      },
      // 
      setNameFormRules: {
        name: [
          { required: true, message: '', trigger: 'blur' }
        ]
      },
     // 
      queryInfo: {
        id_card_number: '',
        name: '',
        pageSize: 10,
        pageNum: 1
      },
      // 
      total: 0,
      // 
      agentList: []
    }
  },
  created() {
    this.gjjzh = window.sessionStorage.getItem('gjjzh')
    this.activeName = !this.$route.query.stateInfo ? 'now' : this.$route.query.stateInfo
    this.getAllowanceList()
  },
  methods: {
    // 
    async getAllowanceList() {
      let that = this
      // 1. 
      this.$http.get('allowance/findAll', {emulateJSON:true, params: {
          gjjzh: this.gjjzh,
          pageNum: this.queryInfo.pageNum,
          pageSize: this.queryInfo.pageSize
        },
      }).then(function(data){
        console.log("========++++======")
        console.log('',data,data.data)
        if (data.data.code !== 200) {
          return that.$message.error('')
        }
        // 1.1
        that.historyAllowanceList = data.data.data.records
        that.total = data.data.data.total
      })
      // 2. 
      const { data: {code, data: currentAllowanceObj} } = await this.$http.get(`/allowance/currentAllowance?gjjzh=${this.gjjzh}`)
      if (code !== 200) return this.$message.info('')
      this.currentAllowanceList.push(currentAllowanceObj)
      if(this.currentAllowanceList.length !==0) {
        // 
        this.createable = true
      }
    },
    // Id
    async removeRightById(role, rightId) {
      // 
      const confirmResult = await this.$confirm(
        ', ?',
        '',
        {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'warning'
        }
      ).catch(err => err)

      if (confirmResult !== 'confirm') {
        return this.$message.info('')
      }

      const { data: res } = await this.$http.delete(
        `roles/${role.id}/rights/${rightId}`
      )

      if (res.meta.status !== 200) {
        return this.$message.error('')
      }

      // this.getRolesList()
      role.children = res.data
    },
    async handleClick(tab, event) {
      console.log(tab, event)
    },
    async revise(allowance) {
      console.log(allowance)
      this.$router.push({path: '/current', query: { gjjzh: allowance.gjjzh } })
    },
    async showAllowance(allowance) {
      console.log(allowance)
      this.$router.push({path: '/subsidiesDetails', query: { id: allowance.allowanceId, gjjzh: allowance.gjjzh} })
    },
    async deleteAllowance(allowance) {
      console.log(allowance)
      const confirmResult = await this.$confirm(
        ', ?',
        '',
        {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'warning'
        }
      ).catch(err => err)

      if (confirmResult !== 'confirm') {
        return this.$message.info('')
      }
    },
    async search() {
      // date,2
      let sdate = null
      let edate=null
      if (this.date) {
         sdate = this.date[0].getFullYear() + '-' + ((this.date[0].getMonth() + 1 + '') .padStart(2, '0')) + '-' + ((this.date[0].getDate()+ '').padStart(2, '0'))
         edate = this.date[1].getFullYear() + '-' + ((this.date[1].getMonth() + 1 + '').padStart(2, '0')) + '-' + ((this.date[1].getDate() + '').padStart(2, '0'))
      }

      let that = this
      // 1. 
      this.$http.get('allowance/findAll', {emulateJSON:true, params: {
          gjjzh: this.gjjzh,
          pageNum: this.queryInfo.pageNum,
          pageSize: this.queryInfo.pageSize,
          startDate: sdate,
          endDate: edate,
          name: this.queryInfo.name,
          id_card_number: this.queryInfo.id_card_number
        },
      }).then(function(data){
        console.log("========++++======")
        console.log('',data,data.data)
        if (data.data.code !== 200) {
          return that.$message.error('')
        }
        // 1.1
        that.historyAllowanceList = data.data.data.records
        that.total = data.data.data.total
        that.$message.success('')
      })
    },
    async clearSearch() {
      this.date = ''
      this.queryInfo.name = ''
      this.queryInfo.id_card_number = ''
    },
    // 
    showSetNameBox() {
      this.setNameDialogVisible = true
    },
    cancelCreateAllowance() {
      this.setNameDialogVisible = false
      this.$message.info('~')
    },
    // 
    setNameDialogClosed() {
    this.$refs.setNameFormRef.resetFields()
    },
    async createAllowance() {
      this.$refs.setNameFormRef.validate( valid => {
        if (!valid) return this.$message.error('')
        //
        this.setNameDialogVisible = false
        let that = this
        let gjjzh = window.sessionStorage.getItem('gjjzh')
        console.log(gjjzh)
        this.$http.get('allowance/createAllowance', { params: { allowanceName: this.setNameForm.name, gjjzh: gjjzh } }, {emulateJSON:true}).then(function(data){
          console.log('',data.data)
          if (data.data.code !== 200) return that.$message.error('')
          that.$message.success('')
          that.getAllowanceList()
        })
      })
    },
    // 
    handleSizeChange(newSize) {
      this.queryInfo.pageSize = newSize
      this.getAllowanceList()
    },
    // 
    handleCurrentChange(newNum) {
      this.queryInfo.pageNum = newNum
      this.getAllowanceList()
    },
    // 
    async showHistoryDialog(queryObj) {
      // 
      const { data: res } = await this.$http.get('/allowance/findAllDlrlistOfBtlist', { params: {
          btcode: queryObj.btcode,
          zbwh: queryObj.zbwh,
          zgbh: queryObj.zgbh
        }})
      if (res.code !== 200) return this.$message.warning('')

      console.log('',res.data)
      this.agentList = res.data
      this.historyDialogVisible = true
    }
  }
}
</script>

<style lang="less" scoped>
.el-tag {
  margin: 7px;
}

.bdtop {
  border-top: 1px solid #eee;
}

.bdbottom {
  border-bottom: 1px solid #eee;
}

.vcenter {
  display: flex;
  align-items: center;
}
</style>
