<template>
  <div>
    <!--  -->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/home' }"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>

    <!--  -->
    <el-card>
      <div style='font-size: 14pt;font-weight:800;display: flex; justify-content: space-between'>
        
        <el-button type="info"  icon="el-icon-message" @click="$router.push('/funds/fundshistory')"></el-button>
      </div>
      <el-row :gutter="20" style='margin-top: 10px;margin-left: 20px'>
        <!---->
        <el-col :span="4">
          <el-row>
            <el-col :span="9">
              <div style='font-weight: bold;margin-top: 10px;font-size: 10pt'></div>
            </el-col>
            <el-col :span="15">
              <el-select v-model="fundType" placeholder="">
                <el-option
                  v-for="(item, index) in fundOptions"
                  :key="index"
                  :label="item.label"
                  :value="item.value">
                </el-option>
              </el-select>
            </el-col>
          </el-row>
        </el-col>
        <!---->
        <!-- <el-col :span="9">
          <el-row>
            <el-col :span="4">
              <div style='font-weight: bold;margin-top: 10px;font-size: 10pt'></div>
            </el-col>
            <el-col :span="20">
              <el-date-picker
                v-model="dateRange"
                type="date"
                placeholder="">
              </el-date-picker>
            </el-col>
          </el-row>
        </el-col> -->
        <!---->
        <el-col :span="6">
          <el-button type="primary" @click="getFundList"></el-button>
          <el-button type="danger" @click="clearSearch"></el-button>
        </el-col>
      </el-row>

      <div style='font-size: 12pt; margin-top:25px' >
        <h3></h3>
        <el-tag :type="tagType" v-if="tagVisable" ref="tagRef">{{fundMsg}}</el-tag>
      </div>
      <div style='font-size: 12pt; margin-top:25px' >
        <h3></h3>
        <el-row type="flex" justify='space-between'>
          <el-col :span="10">
            <el-alert v-if="applyState === 0" title="" type="success" center show-icon :closable="false"></el-alert>
            <el-alert v-else-if="applyState === 1" title="" type="warning" center show-icon :closable="false"></el-alert>
            <el-alert v-else title="" type="error" center show-icon :closable="false"></el-alert>
          </el-col>
          <el-col :span="10">
            <el-button type="primary" :disabled="applyBtnState" @click="applyClickFn"></el-button>
          </el-col>
        </el-row>

      </div>
    </el-card>

    <!--      -->
    <el-dialog title="" :visible.sync="confirmDialogVisible" width="30%">
      <el-form :model="applyForm" :rules="applyFormRules" ref="applyFormRef" label-width="120px">
        <el-form-item label=":" prop="zjje">
          <el-input v-model="applyForm.zjje"></el-input>
        </el-form-item>
        <el-form-item label=":">
          <el-input :value='applyForm.gjjzh' disabled></el-input>
        </el-form-item>
        <el-form-item label=":">
          <el-input :value='applyForm.dwmc' disabled></el-input>
        </el-form-item>
        <el-form-item label=":">
          <el-input :value='fundType' disabled></el-input>
        </el-form-item>
        <el-form-item label=":">
          <el-input :value='dateRange' disabled></el-input>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
          <el-button @click="confirmDialogVisible = false"> </el-button>
          <el-button type="primary" @click="confirmApply"> </el-button>
        </span>
    </el-dialog>
  </div>
</template>

<script>

export default {
  data() {
    return {
      //
      gjjzh: '',
      //
      confirmDialogVisible: false,
      // 
      fundType: '',
      // 
      fundOptions: [
        {
          value: '',
          label: ''
        }, {
          value: '',
          label: ''
        }
      ],
      // 
      dateRange: '',
      // 
      tagVisable: false,
      // 
      fundDetailInfo: {},
      // tag
      tagType: 'primary',
      // 
      applyState: 3,
      // 
      applyNotify: '',
      // 
      applyBtnState: false,
      // 
      amount: 0,
      // 
      fundMsg: '',
      // 
      applyForm: {
        dwmc: sessionStorage.getItem('dwmc'),
        gjjzh: sessionStorage.getItem('gjjzh'),
        sqtype: '',
        zjje: 0
      },
      applyFormRules: {
        zjje: [
          { required: true, message: '', trigger: 'blur' },
          { pattern: /^([1-9]\d{0,9}|0)(\.\d{1,2})?$/, message: '', trigger: 'blur' }
        ]
      }
    }
  },
  async created() {
    this.gjjzh = window.sessionStorage.getItem('gjjzh')
    this.gjjzh = window.sessionStorage.getItem('gjjzh')
    this.getCurrentTime()
    // ,
    const { data: res } = await this.$http.get('/qtzj/fetchApplyStatus',{ params: { gjjzh: this.gjjzh }})
    this.applyState = res.data
    console.log('money',res)
    if (this.applyState === 0) {
      this.applyBtnState = false
    } else {
      // 2\3,
      this.applyBtnState = true
    }
  },
  // 
  methods: {
    // 
    async getFundList() {
      if (this.fundType === '') return this.$message.error('')
      if (this.dateRange === '') return this.$message.error('')
      // const dateFormat = this.dateRange.getFullYear() + '-' + (this.dateRange.getMonth() + 1 + '').padStart(2,'0') + '-' +  (this.dateRange.getDate() + '').padStart(2,'0')
      const { data: res } = await this.$http.get('/qtzj/findQtzjYuE', { params: {
          gjjzh: this.gjjzh ,
          localDate: this.dateRange,
          zjly: this.fundType
        }})
      if( res.code !== 200) return this.$message.error('')
      this.tagVisable =true
      this.fundDetailInfo = res.data
      this.amount = res.data.amount
      this.fundMsg = this.fundType + '' + this.amount
      console.log(123,res)
    },
    // 
    showConfirmDialog() {
      this.confirmDialogVisible = true
    },
    confirmFunds: function() {
      this.tagType = 'success'
      this.$message.success('')
      this.confirmDialogVisible = false;
    },
    // 
    clearSearch() {
      this.dateRange = ''
      this.fundType = ''
      this.tagVisable = false
    },
    //
    getCurrentTime() {
      var nowDate = new Date();
      var date = {
        year: nowDate.getFullYear(),
        month: nowDate.getMonth() + 1,
        day: nowDate.getDate(),
      }
      this.dateRange = date.year + '-' + (date.month >= 10 ? date.month : '0' + date.month) + '-' + (date.day >= 10 ? date.day : '0' + date.day)
    },
    // 
    applyClickFn() {
      if (this.fundType === '') return this.$message.error('')
      this.confirmDialogVisible = true
    },
    // 
    async confirmApply() {
      this.$refs.applyFormRef.validate(async bool => {
        if (bool) {
          // 
          if (this.applyForm.zjje > this.amount || this.applyForm.zjje < 0) return this.$message.warning('')
          const { data: res } = await this.$http.post(`/qtzj/addZjApply?sqrq=${this.dateRange}`, this.applyForm)
          if (res.code !== 200) return this.$message.error('')
          this.$message.success('')
          // 
          this.confirmDialogVisible = false
          this.applyBtnState = true
          this.applyState = 1
        } else {
          return this.$message.warning('')
        }

      })

      // console.log('',res)
    }
  }
}
</script>

<style lang="less" scoped>
/deep/ .el-dialog__body {
  padding: 0 20px!important;
}
.el-tag {
  height: 60px;
  //width: 120px;
  line-height: 60px;
  text-align: center;
  font-size: 18px;
  font-weight: bold;
}

/deep/ .el-alert__title {
  font-size: 21px;
  font-weight: bold;
}

/deep/ .el-form-item__label {
  font-size: 18px;
  font-weight: bold;
}

/deep/ .el-input__inner {
  font-size: 16px;
  font-weight: 600;
}

/deep/ .el-form-item__error {
  font-size: 14px;
}
</style>
