<template>
  <div>
    <!--  -->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/home' }"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>
    <el-card>
      <!--  -->
      <el-button plain icon="el-icon-back" style='margin-bottom: 15px;font-size: 16px;' @click='$router.go(-1)'></el-button>
      <div class="clearfix">
        <el-row style='display: flex!important;align-items: center!important;' :gutter="20">
          <el-col style='display: flex!important;align-items: center!important;' :span="2">
            <h3></h3>
          </el-col>
          <el-col :span="8">
            <el-date-picker
              v-model="date"
              type="daterange"
              range-separator=""
              start-placeholder=""
              end-placeholder="">
            </el-date-picker>
          </el-col>
          <el-col :span="3"><el-button type="primary" size="small" icon="el-icon-search" @click='search'></el-button></el-col>
          <el-col :span="3"><el-button type="danger" size="small" @click='clearSearch'></el-button></el-col>
        </el-row>
      </div>
      <h3></h3>
      <!-- table -->
      <el-table :data="historyList" border stripe :header-cell-style="{background:'#eee',color:'#606266',fontSize: '14px'}">
        <el-table-column type="index"></el-table-column>
        <el-table-column label="" prop="czr" width="100px"></el-table-column>
        <el-table-column label="" prop="dwmc"></el-table-column>
        <el-table-column label="" prop="gjjzh" width="80px"></el-table-column>
        <el-table-column label="" prop="jjcontent"></el-table-column>
        <el-table-column label="" prop="lsh" ></el-table-column>
        <el-table-column label="" prop="num" width="100px"></el-table-column>
        <el-table-column label="" prop="sqrq" width="200px"></el-table-column>
        <el-table-column label="" prop="sqstatus"></el-table-column>
        <el-table-column label="" prop="sqtype"></el-table-column>
        <el-table-column label="" prop="zjje"></el-table-column>
        <el-table-column  label="">
          <template slot-scope="scope">
            <el-button :disabled="scope.row.sqstatus !== ''" type="primary" icon="el-icon-view" size="mini" @click="downloadPdf(scope.row)"></el-button>
          </template>
        </el-table-column>
<!--          <template slot-scope="scope">-->
<!--            {{scope.row.add_time | dateFormat}}-->
<!--          </template>-->
<!--        </el-table-column>-->
<!--        <el-table-column label="" width="130px">-->
<!--          <template slot-scope="scope">-->
<!--            <el-button type="primary" icon="el-icon-edit" size="mini"></el-button>-->
<!--            <el-button type="danger" icon="el-icon-delete" size="mini" @click="removeById(scope.row.goods_id)"></el-button>-->
<!--          </template>-->
<!--        </el-table-column>-->
      </el-table>

      <!--  -->
      <el-pagination @size-change="handleSizeChange" @current-change="handleCurrentChange" :current-page="queryInfo.pageNum" :page-sizes="[5, 10, 15, 20]" :page-size="queryInfo.pageSize" layout="total, sizes, prev, pager, next, jumper" :total="total" background>
      </el-pagination>
    </el-card>
  </div>
</template>

<script>
import toBlob from '@/utils/toblob.js'

export default {
  data() {
    return {
      // 
      queryInfo: {
        gjjzh: sessionStorage.getItem('gjjzh'),
        startDate: '',
        endDate: '',
        pageNum: 1, // 
        pageSize: 10 // 
      },
      // 
      historyList: [],
      // 
      total: 0,
      date: ''
    }
  },
  created() {
    this.getHistoyList()
    this.gjjzh = window.sessionStorage.getItem('gjjzh')
  },
  methods: {
    // 
    async getHistoyList() {
      const { data: res } = await this.$http.get('/qtzj/qureyZjqrlist', {params: this.queryInfo})
      console.log('',res)
      if (res.code !== 200) {
        return this.$message.error('')
      }
      if (res.data.total === 0) {
        return this.$message.error('')
      }
      this.$message.success('')
      this.historyList = res.data.records
      this.total = res.data.total
    },
    handleSizeChange(newSize) {
      this.queryInfo.pageSize = newSize
      this.getHistoyList()
    },
    handleCurrentChange(newPage) {
      this.queryInfo.pageNum = newPage
      this.getHistoyList()
    },
    async search() {
      // date,2
      if (!this.date) return this.$message.warning(' ')
      this.queryInfo.startDate = this.date[0].getFullYear() + '-' + ((this.date[0].getMonth() + 1 + '') .padStart(2, '0')) + '-' + ((this.date[0].getDate()+ '').padStart(2, '0'))
      this.queryInfo.endDate = this.date[1].getFullYear() + '-' + ((this.date[1].getMonth() + 1 + '').padStart(2, '0')) + '-' + ((this.date[1].getDate() + '').padStart(2, '0'))
      this.getHistoyList()
    },
    // 
    clearSearch() {
      this.date = ''
      // 
      this.queryInfo.startDate = ''
      this.queryInfo.endDate = ''
      this.getHistoyList()
    },
    // 
    async downloadPdf(obj) {
      const { data: res } = await this.$http.post('/qtzj/printCertificate', obj)
      console.log('',res)
      // 1. toBlobbase64Blob
      let fileURL = window.URL.createObjectURL(toBlob(res.data))
      // pdf
      window.open(fileURL)

      // 2. 
      let link = document.createElement('a')  //
      link.style.display = 'none'   // 
      link.href = fileURL  // 
      //  a href+download download
      link.setAttribute('download',  ''+ '.pdf')
      document.body.appendChild(link) // 
      link.click()  // download
      document.body.removeChild(link) // 
      window.URL.revokeObjectURL(fileURL) // blob
    }
  }
}
</script>

<style lang="less" >
  .worker-item .el-form-item__label{
    font-weight: bold;
    font-size: 16px;
  }
</style>
