<template>
  <div>
    <!--  -->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/home' }"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>
    <!--  -->
    <el-card>
      <el-row :gutter="30">
        <el-col :span="6" >
          <el-form ref="form"  label-width="90px">
            <el-form-item label=":" class='worker-item'>
              <el-input placeholder="" v-model="queryInfo.name" clearable></el-input>
            </el-form-item>
          </el-form>
        </el-col>
        <!-- <el-col :span="6" >
          <el-form ref="form"  label-width="50px" >
            <el-form-item label=":" class='worker-item'>
              <el-select v-model="queryInfo.sex" clearable placeholder="">
                <el-option v-for="(item,index) in sexOptions" :key="index"  :value="item"></el-option>
              </el-select>
            </el-form-item>
          </el-form>
        </el-col> -->
        <el-col :span="6" >
          <el-form ref="form"  label-width="90px" >
            <el-form-item label=":" class='worker-item'>
              <el-input placeholder="" v-model="queryInfo.idNumber" clearable></el-input>
            </el-form-item>
          </el-form>
        </el-col>
        <el-col :span="3">
          <el-button type="primary" @click="findWorker"></el-button>
        </el-col>
        <el-col :span="3">
          <el-button type="danger" @click="clearFilter"></el-button>
        </el-col>
      </el-row>
      <h3></h3>
      <!-- table -->
      <el-table :data="workerList" border stripe :header-cell-style="{background:'#eee',color:'#606266',fontSize: '14px'}">
        <el-table-column type="index" label='' width="60px"></el-table-column>
        <el-table-column label="" prop="name"></el-table-column>
        <el-table-column label="" prop="idNumber"></el-table-column>
        <el-table-column label="" prop="cardNumber"></el-table-column>
        <!--        <el-table-column label="" prop="add_time" width="140px">-->
<!--          <template slot-scope="scope">-->
<!--            {{scope.row.add_time | dateFormat}}-->
<!--          </template>-->
<!--        </el-table-column>-->
<!--        <el-table-column label="" width="130px">-->
<!--          <template slot-scope="scope">-->
<!--            <el-button type="primary" icon="el-icon-edit" size="mini"></el-button>-->
<!--            <el-button type="danger" icon="el-icon-delete" size="mini" @click="removeById(scope.row.goods_id)"></el-button>-->
<!--          </template>-->
<!--        </el-table-column>-->
      </el-table>

      <!--  -->
      <el-pagination @size-change="handleSizeChange" @current-change="handleCurrentChange" :current-page="queryInfo.pageNum" :page-sizes="[5, 10, 15, 20]" :page-size="queryInfo.pageSize" layout="total, sizes, prev, pager, next, jumper" :total="total" background>
      </el-pagination>
    </el-card>
  </div>
</template>

<script>
export default {
  data() {
    return {
      // 
      queryInfo: {
        name: '',
        idNumber: '',
        // sex: '',
        gjjzh: '',
        pageNum: 1,
        pageSize: 10
      },
      sexOptions: ['',''],
      // 
      workerList: [],
      // 
      total: 0
    }
  },
  created() {
    this.getWorkerList()
    this.gjjzh = window.sessionStorage.getItem('gjjzh')
  },
  methods: {
    // 
    async getWorkerList() {
      const { data: { data: res }, data: meta } = await this.$http.post('/employee/findEmployeeList', this.queryInfo
      )
      console.log(res)
      if (meta.code !== 200) {
        return this.$message.error('')
      }
      if (res.total == 0) {
        return this.$message.error('')
      }
      this.$message.success('')
      this.workerList = res.records;
      // 
      this.total = res.total
    },
    handleSizeChange(newSize) {
      this.queryInfo.pageSize = newSize
      this.getWorkerList()
    },
    handleCurrentChange(newPage) {
      this.queryInfo.pageNum = newPage
      this.getWorkerList()
    },
    findWorker() {
      this.getWorkerList()
    },
    clearFilter() {
      this.queryInfo.name = '';
      this.queryInfo.idNumber = '';
      this.queryInfo.sex = '';
      this.getWorkerList()
    }

  }
}
</script>

<style lang="less" >
  .worker-item .el-form-item__label{
    font-weight: bold;
    font-size: 16px;
  }
</style>
